    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
body {
    width: 100%;
    margin: 0;
    color: #f4f4f4;
    background: #222;
}

#app,
#app > div {
    display: block;
    position: relative;
    width: 100%;
    height: 100vh;
}
#app > div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
#app > div > * {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
}
.main {
    max-width: 960px;
    margin: 0.5em;
    -ms-flex-item-align: center;
        align-self: center;
}

a {
    text-decoration: none;
    color: #f4f4f4;
}

h3 {
    font-variant-caps: petite-caps;
    font-weight: bold;
    text-align: center;
}

#footer {
