.btn {
    border-radius: 4px;
    /* font-family: Arial; */
    color: #f4f4f4;
    font-size: 1.1em;
    padding: 8px 8px 8px 8px;
    text-decoration: none;
}
.btn:hover {
    text-decoration: none;
}
.btn.btn-linkedin {
    background: #0084bf;
}
.btn.btn-twitter {
    background: #1da1f2;
}
.btn.btn-github {
    background: #24292e;
}
.btn.btn-youtube {
    background: #ff0000;
}
.btn.btn-instagram {
    background: #bd3381;
    background-image: -webkit-radial-gradient(at 70% 70%, #ee583f 8%, #d92d77 42%, #bd3381 58%);
    background-image: -o-radial-gradient(at 70% 70%, #ee583f 8%, #d92d77 42%, #bd3381 58%);
    background-image: radial-gradient(at 70% 70%, #ee583f 8%, #d92d77 42%, #bd3381 58%);
}

#header {
    /* height: 2em; */
    /* position: fixed; */
    /* top: 0; */
