}
nav a:hover,
nav .button:hover {
    color: #9393ff;
}

#menu {
    display: flex;
    display: -ms-flexbox;
    -ms-flex-direction: row;
        flex-direction: row;
    border-radius: 4px;
}

#menu > .item {
    -ms-flex-order: 1;
        order: 1;
    -ms-flex-positive: 1;
        flex-grow: 1;
    text-align: center;
    font-size: 0.8em;
    /* font-variant-caps: all-small-caps; */
    text-transform: uppercase;
}

#menu > .item-logo {
    -ms-flex-positive: 3;
        flex-grow: 3;
    text-align: left;
    font-size: 1em;
}

.anchor-link {
    display: none;
    padding: 0 1em;
    text-align: center;
    padding: 10px 15px;
    text-decoration: none;
    margin: 3px;
}
.anchor-link#menu-toggle {
    float: right;
    color: #fff;
    background-color: #0000e6;
    border-radius: 4px;
}
.anchor-link.item-logo {
    float: left;
    text-transform: uppercase;
}

.anchor-link.item-lang {
    position: absolute;
    right: 60px;
    font-size: 0.7em;
}

#intro {
    width: 100%;
    overflow: hidden;
    /* margin: auto; */
    /* justify-content: center; */
    /* align-items: center; */
}
#intro > div {
    padding: 0.5em;
    /* text-align: center; */

    display: flex;
    display: -ms-flexbox;
    -ms-flex-direction: row;
