#intro div.title > * {
    -ms-flex: 1 1;
        flex: 1 1;
    /* border: 1px solid goldenrod; */
    font-size: 1.5em;
    font-weight: bold;
}
#intro div.title > span.location {
    -ms-flex: 2 1;
