    font-size: 1em;
    padding-top: 2em;
}
#intro div.pic {
    height: auto;
    text-align: left;
}
#intro div.pic > img {
    max-width: 100%;
    border-radius: 4px;
}

#about h4 {
    color: #9393ff;
    /* text-shadow: 0 0 20px rgba(207, 207, 207, 0.5); */
}
#about h5,
#about h6 {
    margin: 1em 0;
}
#about div.experience p {
    font-size: 0.6em;
}
#about div.experience li {
