    width: 33%;
}
#skills .dev-skills {
    text-align: center;
}

#portfolio .portfolio-examples {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-line-pack: start;
        align-content: flex-start;
}
#portfolio .portfolio-example-item {
    /* max-width: 50%; */
    margin: 10px 5px;
    max-height: 400px;
    padding: 3px;
    border-radius: 4px;
    -webkit-box-shadow: 0px 0px 6px 3px #0000e6;
            box-shadow: 0px 0px 6px 3px #0000e6;
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
#portfolio .portfolio-example-item:hover {
    -webkit-box-shadow: 0px 0px 9px 6px #0000e6;
            box-shadow: 0px 0px 9px 6px #0000e6;
}
/* #portfolio .portfolio-example-item > * {
    flex-grow: 1;
} */
#portfolio .portfolio-example-medias {
    /* flex-grow: 0.5; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}
#portfolio .portfolio-example-img {
    min-width: 0%;
}
#portfolio .portfolio-example-img img {
    height: 100%;
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}
#portfolio .portfolio-example-title {
    font-size: 1em;
    margin: 10px 10px;
}
#portfolio .portfolio-example-desc {
    font-size: 0.7em;
    margin: 5px 10px;
}
#portfolio .portfolio-example-tech {
    font-size: 0.8em;
    font-style: italic;
    margin: 5px 10px;
}

#contact {
    text-align: center;
}
#contact a {
    color: #9393ff;
    font-weight: bold;
}
#contact a:hover {
    color: #f4f4f4;
}

/* Mobile Standards */
@media (max-width: 768px) {
    #menu {
        display: none;
    }
    .anchor-link {
        display: block;
    }

    ul.open {
        background-color: #f4f4f4;
        -webkit-box-shadow: 2px 2px 3px #444444;
                box-shadow: 2px 2px 3px #444444;
        display: block !important;
        /* list-style: none outside none; */
        margin: 0;
        padding: 0;
        position: absolute;
        right: 3px;
        /* top: 5px; */
        width: 60%;
        z-index: 500;
    }
    ul.open li {
        display: block;
        list-style: none;
        text-align: center;
    }
    ul.open li a {
        display: block;
        padding: 20px 10px;
        border-bottom: 1px solid #222;
        text-decoration: none;
        color: #222;
    }
    ul.open li a:hover {
        background-color: #222;
        color: #fff;
    }
    ul.open li.item-logo,
    ul.open li.item-lang {
        display: none;
    }

    #intro {
        width: 100%;
        padding: 0;
    }
    #intro div.title > * {
        font-size: 1.2em;
    }
    #intro div.title > span.location {
        font-size: 0.7em;
    }
    #intro div.title > span.location > i {
        vertical-align: bottom;
        font-size: 1.1em;
    }

    #skills table {
        width: 100%;
    }
}
@media (max-width: 480px) {
    /* #intro > div {
        padding: 0;
    } */
    #footer ul.social {
        -ms-flex-direction: column;
            flex-direction: column;
    }
}
