button#scroll-top-btn {
    display: none; /* Hidden by default */
    position: fixed; /* Fixed/sticky position */
    bottom: 3em; /* Place the button at the bottom of the page */
    right: 1em; /* Place the button 30px from the right */
    z-index: 99; /* Make sure it does not overlap */
    border: none; /* Remove borders */
    outline: none; /* Remove outline */
    background-color: #b3b2b2; /* Set a background color */
    opacity: 0.5;
    color: #fff; /* Text color */
    cursor: pointer; /* Add a mouse pointer on hover */
    padding: 2em; /* Some padding */
    border-radius: 10px; /* Rounded corners */
}
button#scroll-top-btn:hover {
    opacity: 1;
}

.material-icons {
    vertical-align: top;
}
.material-icons.md-18 {
    font-size: 18px;
}
.material-icons.md-24 {
    font-size: 24px;
}
.material-icons.md-36 {
    font-size: 36px;
}
.material-icons.md-48 {
    font-size: 48px;
}

/* Inspired by the Stripe.com website */
#stripes,
#stripes span {
    position: absolute;
}
#stripes {
    width: 100%;
    height: 100%;
    overflow: hidden;
    -webkit-transform: skewY(-10deg);
        -ms-transform: skewY(-10deg);
            transform: skewY(-10deg);
    -webkit-transform-origin: 0;
        -ms-transform-origin: 0;
            transform-origin: 0;
    background: -webkit-linear-gradient(300deg, #000080 15%, #0000e6 70%, #88bcfd 94%);
    background: -o-linear-gradient(300deg, #000080 15%, #0000e6 70%, #88bcfd 94%);
    background: linear-gradient(150deg, #000080 15%, #0000e6 70%, #88bcfd 94%);
}
#stripes span {
    height: 40px;
}
#stripes :nth-child(1) {
    width: 50%;
    left: 50%;
    top: 40px;
    background: #000099;
}
#stripes :nth-child(2) {
    width: 14%;
    right: 0;
    bottom: 40px;
    background: #0000e6;
}
#stripes :nth-child(3) {
    width: 25%;
    bottom: 0;
    right: 0;
    background: #000099;
}
@media (min-width: 670px) {
    #stripes span {
        height: 150px;
    }
    #stripes :nth-child(1) {
        top: 0;
        background: #000099;
    }
    #stripes :nth-child(2) {
        width: 16.66667%;
        top: 300px;
        bottom: auto;
        background: #0000e6;
    }
    #stripes :nth-child(3) {
        width: 33.33333%;
        right: auto;
        background: #0000e6;
    }
}
@media (min-width: 880px) {
    #stripes span {
        height: 190px;
    }
    #stripes :nth-child(1) {
        width: 33.33333%;
        left: -16.66666%;
        background: #000080;
    }
    #stripes :nth-child(2) {
        width: 33.33333%;
        top: 0;
        left: 16.66666%;
        right: auto;
        background: #000099;
    }
    #stripes :nth-child(3) {
        width: 33.33333%;
        left: 49.99999%;
        bottom: auto;
        background: #000066;
    }
    #stripes :nth-child(4) {
        width: 33.33333%;
        top: 200px;
        right: -16.66666%;
        background: #0000e6;
    }
    #stripes :nth-child(5) {
        width: 33.33333%;
        bottom: 0;
        background: #0000e6;
    }
}



